.functional__list {
  display: grid;
  margin: 0;
  padding: 0;
  color: $special-grey-700;
  list-style-type: none;

  @media (max-width: $width-max-mobile) {
    row-gap: 20px;
  }

  @media (min-width: $width-tablet) {
    grid-template-columns: 1fr 1fr;
    grid-gap: 31px 60px;
    font-size: 16px;
    line-height: 24px;
  }

  @media (min-width: $width-pre-desktop) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 80px;
  }
}

.functional__item {
  display: grid;

  @media (max-width: $width-max-mobile) {
    grid-template-columns: 60px 1fr;
    column-gap: 20px;
  }

  @media (min-width: $width-tablet) {
    grid-template-rows: 80px 1fr;
    row-gap: 31px;
    height: 286px;
    padding-right: 80px;
  }

  @media (min-width: $width-pre-desktop) {
    padding-right: 0;
  }

  &::before {
    content: "";
    background: $basic-green no-repeat center;

    @media (max-width: $width-max-mobile) {
      width: 60px;
      height: 60px;
      margin-top: -2px;
    }

    @media (min-width: $width-tablet) {
      width: 80px;
      height: 80px;
    }
  }

  &--nutrient::before {
    background-image: url("../../img/icons/ico-leaf.svg");
  }

  &--powder::before {
    background-image: url("../../img/icons/ico-tub.svg");
  }

  &--diet::before {
    background-image: url("../../img/icons/ico-eat.svg");
  }

  &--result::before {
    background-image: url("../../img/icons/ico-alarm.svg");
  }
}

.functional__title {
  @media (max-width: $width-max-mobile) {
    margin-bottom: 40px;
    font-size: 36px;
  }

  @media (min-width: $width-tablet) {
    margin-bottom: 72px;
    font-size: 60px;
  }
}
